import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useMutation } from "react-query";
import { observer } from "mobx-react";
import { useStores } from "../state/Context";

const Login = () => {
  let navigator = useNavigate();
  const { DataStore } = useStores();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const emailHandler = (e) => {
    setEmail(e.target.value);
  };
  const passwordHandler = (e) => {
    setPassword(e.target.value);
  };
  const { mutate } = useMutation(({ email, password }) => {
    return axios.post(`http://${process.env.REACT_APP_HOST_URL}/auth/login`, {
      email,
      password,
    });
  });
  const onSubmit = async (e) => {
    e.preventDefault();
    mutate(
      { email, password },
      {
        onSuccess: () => {
          DataStore.setLoggedin(true);
          navigator("/");
        },
      }
    );
  };
  return (
    <div>
      <form onSubmit={onSubmit}>
        <span>email</span>
        <input type="email" value={email} onChange={emailHandler}></input>
        <br />
        <span>password</span>
        <input type="text" value={password} onChange={passwordHandler}></input>
        <br />
        <button type="submit">sign in</button>
        <button
          type="button"
          onClick={() => {
            navigator("/join");
          }}
        >
          Go To The Register Page
        </button>
      </form>
    </div>
  );
};
export default observer(Login);
