import Taskdisplay from "./Taskdisplay";
import axios from "axios";
import { useEffect, useState } from "react";
import { useQuery } from "react-query";
import { useFetchTasks } from "../queries/useFetchTasks";
import { observer } from "mobx-react";
import { TaskStore } from "../state/TaskStore";
import { useStores } from "../state/Context";

const Tasks = () => {
  const { TaskStore, DataStore } = useStores();
  const refetchAll = useFetchTasks("");
  const refetchNear = useFetchTasks("nearness");
  useEffect(() => {
    refetchAll();
  }, []);

  return (
    <div>
      <h1>Todo</h1>
      <button type="button" onClick={() => DataStore.setWorking("Hashtag")}>
        Hashtag
      </button>
      <div className="root">
        <button type="button" onClick={() => refetchAll()}>
          Get All Tasks
        </button>
        <button type="button" onClick={() => refetchNear()}>
          Get Nearness
        </button>
      </div>
      <Taskdisplay />
    </div>
  );
};
export default observer(Tasks);
