import "./Header.css";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faListCheck } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { useStores } from "../state/Context";
import { observer } from "mobx-react";

const Header = () => {
  const { DataStore } = useStores();
  let navigate = useNavigate();
  const onClick = () => navigate("/login");
  const onClickHeader = () => {
    navigate("/");
  };

  const onLogout = async () => {
    DataStore.setLoggedin(false);
    await axios.get(`http://${process.env.REACT_APP_HOST_URL}/auth/logout`);
    navigate("/");
  };
  return (
    <div className="Header__container">
      <FontAwesomeIcon icon={faListCheck} size="5x" />
      <h1 onClick={onClickHeader}>Todo List</h1>

      {DataStore.loggedIn ? (
        <button type="button" onClick={onLogout}>
          <h2>Logout</h2>
        </button>
      ) : (
        <button type="button" onClick={onClick}>
          <h2>Login</h2>
        </button>
      )}
    </div>
  );
};
export default observer(Header);
