import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useMutation } from "react-query";
import { observer } from "mobx-react";

const Register = () => {
  let navigator = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [nick, setNick] = useState("");
  const emailHandler = (e) => {
    setEmail(e.target.value);
  };
  const passwordHandler = (e) => {
    setPassword(e.target.value);
  };
  const nickHandler = (e) => {
    setNick(e.target.value);
  };
  const { mutate } = useMutation(({ email, password }) => {
    return axios.post(`http://${process.env.REACT_APP_HOST_URL}/auth/join`, {
      email,
      password,
      nick,
    });
  });
  const onSubmit = async (e) => {
    e.preventDefault();
    mutate({ email, password, nick });
  };
  return (
    <div>
      <form onSubmit={onSubmit}>
        <span>email</span>
        <input type="email" value={email} onChange={emailHandler}></input>
        <br />
        <span>password</span>
        <input type="text" value={password} onChange={passwordHandler}></input>
        <br />
        <span>nick</span>
        <input type="text" value={nick} onChange={nickHandler}></input>
        <br />
        <button type="submit">sign in</button>
        <button
          type="button"
          onClick={() => {
            navigator("/login");
          }}
        >
          Go To the Login Page
        </button>
      </form>
    </div>
  );
};
export default observer(Register);
