import { action, makeObservable, observable, runInAction } from "mobx";

export class DataStore {
  rootStore;
  // user = {};
  loggedIn = false;
  id = 0; //current Working
  working = "Tasks";
  constructor(root) {
    makeObservable(this, {
      loggedIn: observable,
      id: observable,
      working: observable,

      setLoggedin: action,
      setId: action,
      setWorking: action,
    });

    this.rootStore = root;

    // this.user = new Loggedin(false);
  }
  setLoggedin(value) {
    // this.user = new Loggedin(value);
    runInAction(() => {
      this.loggedIn = value;
    });
  }
  setId(value) {
    runInAction(() => {
      this.id = value;
    });
  }
  setWorking(value) {
    runInAction(() => {
      this.working = value;
    });
  }
}
