import { action, makeObservable, observable, runInAction } from "mobx";

export class TaskStore {
  rootStore;
  // user = {};
  Tasks = [];

  constructor(root) {
    makeObservable(this, {
      Tasks: observable,
      setTasks: action,
    });

    this.rootStore = root;
    this.Tasks = [];
  }
  setTasks(value) {
    runInAction(() => {
      if (value === undefined || value === null) {
        this.Task = [];
      } else {
        this.Tasks = value;
      }
    });
  }
  length() {
    return this.Tasks.length;
  }
}
